import React, { useContext } from 'react';
import PropTypes from 'prop-types';

import { LocalizationContext } from '../../lib/LocalizationContext';
import Label, { LabelTypography, LabelColors } from '../Label';
import IconButton from '../IconButton';

import './index.scss';
import Avatar from '../Avatar/index';

export default function ChannelHeader({
  user,
  iconButton,
  onEdit,
  renderHeader,
  allowProfileEdit,
}) {
  const { stringSet } = useContext(LocalizationContext);
  return (
    <div
      className={`
        ${allowProfileEdit && 'sendbird-channel-header--allow-edit'}
        sendbird-channel-header
      `}
    >
      {
        renderHeader
          ? renderHeader()
          : (
            <div
              className="sendbird-channel-header__title"
              role="button"
              onKeyDown={onEdit}
              onClick={onEdit}
              tabIndex="0"
            >
              <div className="sendbird-channel-header__title--left">
                <Avatar
                  height="32px"
                  width="32px"
                  src={user.profileUrl}
                  alt={user.nickname}
                />
              </div>
              <div className="sendbird-channel-header__title--right">
                <Label
                  className="sendbird-channel-header__title--name"
                  type={LabelTypography.SUBTITLE_2}
                  color={LabelColors.ONBACKGROUND_1}
                >
                  {user.nickname || stringSet.NO_NAME}
                </Label>
                <Label
                  className="sendbird-channel-header__title--user-id"
                  type={LabelTypography.BODY_2}
                  color={LabelColors.ONBACKGROUND_2}
                >
                  {user.userId}
                </Label>
              </div>
            </div>
          )
      }
      <div className="sendbird-channel-header__right-icon">
        {iconButton}
      </div>
    </div>
  );
}

ChannelHeader.propTypes = {
  user: PropTypes.shape({
    profileUrl: PropTypes.string,
    nickname: PropTypes.string,
    userId: PropTypes.string,
  }),
  renderHeader: PropTypes.func,
  allowProfileEdit: PropTypes.bool,
  onEdit: PropTypes.func.isRequired,
  iconButton: PropTypes.oneOfType([
    PropTypes.element,
    PropTypes.instanceOf(IconButton),
  ]),
};

ChannelHeader.defaultProps = {
  user: {},
  renderHeader: null,
  allowProfileEdit: false,
  iconButton: null,
};
